body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* TODO: Switch to JSS */

.about {
    height: 100%;
    width: 60%;
    float: right;
	overflow-y: auto;
    font-family: sans-serif;
    margin-top: 20px;
}

.aboutHeader{
    padding: 20px 10px;
    text-align: center;
}

.mediaLinks {
    margin-top: 30px;
    text-align: center;
    font-size: 13px;
    letter-spacing: 1px;
    text-decoration: none;
}


.name {
    background: linear-gradient(130deg, #ff7c00 0%, #e93750 30%, #b00970 60%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 700;
    padding-bottom: 10px;
    text-align: center;
}

.aboutDetails{
    font-size: 16px;
    color: #363636;
    text-align: left;
    padding: 0 42px;
    line-height: 22px;
}

img.headshot {
    width: 30%;
    border-radius: 50%;
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

 
a {
    text-decoration: none;
    color: #b00970;
}
a:hover {
    text-decoration: underline;
}

.hi {
    font-size: 30px;
    font-family: 'Playfair Display', serif;
    color: #000;
}

span.space{
    padding: 0 5px;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */

    .about {
        width: 100%;
        height: 100%;
        overflow-y: auto;
        margin: 65% 0 10% 0%;
    }

    .aboutHeader{
        text-align: center;
    }

    .mediaLinks {
        padding: 10px;
        text-align: center;
        font-size: 14px;
        letter-spacing: 1px;
    }
    
    .name {
        background: linear-gradient(130deg, #ff7c00 0%, #e93750 30%, #b00970 60%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 700;
    }


    .aboutDetails{
        font-size: 17px;
        color: #363636;
        text-align: left;
        padding: 0 32px;
    }

}
/* TODO: Switch to JSS */

@media (min-width: 769px) {

  .details {
    padding: 50px 30px;
    height: 100%;
    width: 50%;
    float: right;
    overflow-y: auto;
    text-align: left;
    color: #464646;
  }

  .desc {
    padding: 60px 20px;
    text-align: center;
  }

  .projectDesc {
    padding: 10px;
  }

}

@media (max-width: 768px) {
  .content {
    height: 100%;
    /*background-color: #fff;*/
    overflow-y: auto;
    padding: 70% 10% 20% 10%;
    color: #464646;
    float: left;
  }


  .projectDesc {
    padding: 10px;
    font-size: 15px;
  }
}


.launchApp {
    margin: auto;
    display: block;
    border-radius: 34px;
    width: 130px;
    height: 30px;
    font-size: 13px;
    font-weight: bold;
    background-color: #d63939;
    border: none;
    color: #ffffff;
}

button>a {
    color: #ffffff;
}

button>a:hover {
    color: #d63939;
}

.launchApp:hover {
    border: 3px solid #d63939;
    background-color: transparent;
    color: #d63939;
}

.showcaseApps {
    display: block;
    text-align: center;
    margin-top: 60px;
}
/* TODO: Switch to JSS */

a:hover {
	text-decoration: none;
	font-weight: bold;
}

@media (min-width:769px) {
	/* tablet, landscape iPad, lo-res laptops ands desktops */
	.menuContainer {
		width: 50%;
		height: 100vh;
		float: left;
		background-image: -webkit-linear-gradient(165deg, transparent 34.8%, #d63939 35%, #ff975e 99%);
		position: fixed;
		overflow: hidden !important;
		z-index: 1000;
	}

	.imgGallery {
		width: 30%;
		float: left;
		margin: 4px;
		-webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
		filter: grayscale(100%);
	}

	.menu {
		float: left;
		margin-top: 300px;
		margin-left: 30px
	}

	.menuLink {
		display: block;
		margin: 15px;
		text-decoration: none;
		color: #fff;
		font-size: 16px;
		letter-spacing: 1px;
		font-weight: lighter;
	}

}


@media (max-width: 768px) {
	/* For mobile phones: */
	.menuContainer{
		width: 100%;
		height: 300px;
		background-image: -webkit-linear-gradient(100deg, transparent 34.5%, #d63939 35%, #ff975e 95%);
		position: fixed;
		overflow: hidden;
	}

	.menu {
		float: left;
		margin-top: 25px;
		margin-left: 20px;
		z-index: 200;
	}

	.menuLink {
		display: block;
		margin: 15px 15px;
		text-decoration: none;
		color: #fff;
		font-size: 16px;
		letter-spacing: 1px;
		font-weight: lighter;
	}

}


